import {createSlice} from "@reduxjs/toolkit"
import {filterPhoto} from "../../asyncActionCreator"

const initialState = {
    filterPhoto: {},
    isLoadingFilter: true,
    error_msg: '',
    error_status: ''
}

export const photoFilterSlice = createSlice({
    name: 'photo_filter',
    initialState,
    reducers: {},
    extraReducers: {
        [filterPhoto.fulfilled.type]: (state, action) => {
            state.isLoadingFilter = false;
            state.error = ''
            state.filterPhoto = action.payload
        },
        [filterPhoto.pending.type]: (state, action) => {
            state.isLoadingFilter = true
        },
        [filterPhoto.rejected.type]: (state, action) => {
            state.isLoadingFilter = false
            state.error_msg = action.payload
            state.error_status = 'rejected'
        }
    }
})

export default photoFilterSlice.reducer