import "./styles/main.sass"

import {Col, Row} from "react-bootstrap"
import {Section} from "../../components/section"

import {ImgsUploader} from "./sections/imgs_uploader/ImgsUploader"
import {FilterDetail} from "./sections/filter_detail/FilterDetail"
import {FancyBoxSlider} from "../../components/fancybox-slider/FancyBoxSlider"

import {useEffect, useState} from "react"

import {useDispatch, useSelector} from "react-redux"
import {fetchMeta} from "../../redux/reducers/asyncActionCreator"

import {SliderInfo} from "./sections/slider_info/SliderInfo"

export function FinderPage(){

    const dispatch = useDispatch()

    const [stage, setStage] = useState(1) 

    const {id_object, isLoadingPhoto} = useSelector(state => state.reducers.photoUploadSlice)
    const {meta, isLoadingMeta} = useSelector(state => state.reducers.meta)
    const {filterPhoto, isLoadingFilter, error_status, error_msg} = useSelector(state => state.reducers.photoFilterSlice)
    const {is_testdata} = useSelector(state => state.reducers.finderPage)
   
    useEffect(() => {
        if(stage == 2){
            dispatch(fetchMeta())
        }
    }, [stage])


    return(
        <Section className={"finder"}>
            <Row className={"finder__wrapper"}>
                <Col xs={12} lg={8}>
                    <Row className={"finder__title"}>
                        Сервис для поиска пропавших животных
                    </Row>
                    <Row className={"finder__content"}>
                        {stage === 1 &&
                            <ImgsUploader
                                isLoadingPhoto={isLoadingPhoto}
                                setStage={setStage}
                                id_object={id_object}
                            />
                        }
                        {stage === 2 &&
                            <FilterDetail 
                                meta={meta}
                                isLoadingMeta={isLoadingMeta}
                                setStage={setStage}
                                isLoadingFilter={isLoadingFilter}
                                id_object={id_object}
                                is_testdata={is_testdata}
                            /> 
                        }
                        {stage === 3 &&
                            <SliderInfo
                                setStage={setStage}
                                filterPhoto={filterPhoto}
                                error_status={error_status}
                                error_msg={error_msg}
                            />
                        }
                    </Row>
                </Col>
            </Row>
        </Section>
    )
}