import "./styles/main.sass"
import {Row, Col} from "react-bootstrap"
import {useEffect, useState } from "react"

import ImageUploader from 'react-images-upload'
import {FilterButton} from "../../../../components/button/FilterButton"
import Loader from "../../../../components/loader/Loader"

import {useDispatch} from "react-redux"
import {uploadPhoto} from "../../../../redux/reducers/asyncActionCreator"

import {testDataToggle} from "../../../../redux/reducers/modules/finder_page/actions"

export function ImgsUploader({isLoadingPhoto, setStage, id_object}){
    const dispatch = useDispatch()

    const [pictures, setPictures] = useState([])

    const onDrop = picture => {
        setPictures(picture)
    }
    
    useEffect(() => {
        /*if  photo is upload toggle next stage 
            if id_object !== undefined, photos found
        */
        if((isLoadingPhoto == false) && (id_object !== undefined)){
            setStage(2)
        }
    }, [isLoadingPhoto])

    const submitHandler = () => {
        /* upload imgs submit button */
        if(pictures.length > 0){
            let imagedata = document.querySelector('input[type="file"]').files
            dispatch(uploadPhoto(imagedata))
        }
    }

    const submitHandlerTestData = () => {
        /* upload imgs submit button */
        dispatch(testDataToggle(true))
        setStage(2)
    }

    return(
        <Row>
            <Col xs={12}>
                <ImageUploader
                    singleImage={false}
                    withIcon={true}
                    multiple={true}
                    withPreview={true}
                    buttonText='Выберите фотографии питомца'
                    onChange={onDrop}
                    imgExtension={['.jpg','.png','.jpeg']}
                    maxFileSize={3145728}
                />
                <Row className={"filter_detail_box pt-3"}>
                    <Col xs={6}>
                        {isLoadingPhoto === true && 
                            <Loader/>
                        }
                        {id_object === undefined && 
                            <Row className={"animal_undefined"}>
                                <p>
                                    Питомец не найден :( , попробуйте еще
                                </p>
                            </Row> 
                        }
                    </Col>
                    <Col xs={3} className={"m-auto ml-5"}>
                        <FilterButton
                            className={"filter_detail__datatest"}
                            onClick={submitHandlerTestData}
                        >
                            Тестовый датасет
                        </FilterButton>
                    </Col>

                    <Col xs={3} className={"m-auto"}>
                        <FilterButton
                            onClick={submitHandler}
                        >
                            Найти питомца:)
                        </FilterButton>
                    </Col>
                </Row>
            </Col>
        </Row>
    )
}